.add-admin-container {
    max-width: 400px;
    margin: auto;
    margin-top: 180px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #fff;

    h2 {
        text-align: center;
        margin-bottom: 20px;
    }
    
    .admin-form {
        display: flex;
        flex-direction: column;
    }
    
    .form-group {
        margin-bottom: 15px;
    }
    
    label {
        margin-bottom: 5px;
        font-weight: bold;
    }
    
    input {
        padding: 10px;
        border: 1px solid #ccc;
        border-radius: 4px;
        width: 100%;
        box-sizing: border-box;
    }
    
    input:focus {
        border-color: green;
        outline: none;
    }
    
    .submit-button {
        padding: 10px;
        border: none;
        border-radius: 4px;
        background-color: rgb(23, 164, 23);
        color: white;
        cursor: pointer;
        transition: background-color 0.3s;
        font-size: 17px;
    }
    
    .submit-button:hover {
        background-color: rgb(24, 185, 61);
    }
    
    .message {
        text-align: center;
        margin-top: 10px;
        color: green;
    }
    
}


@media (max-width: 768px){
    .add-admin-container{
        margin-left: 170px;
        margin-top: 265px;
    }
}
@media (max-width: 480px) {
    .add-admin-container {
        padding: 15px;
        margin-left: 20px;
        margin-right: 20px;
    }

    h2 {
        font-size: 1.5em;
    }

    .submit-button {
        font-size: 0.9em;
    }
}
